import React from 'react';

import '../../../../scss/css_challenges/lightsout.scss';

const N = 12;

const LightsOut = ({ showExplanation }) => {
  const inputRefs = [];
  const setRef = (ref) => {
    inputRefs.push(ref);
  };

  const toggleController = () => {
    const controllerRef = inputRefs[0];
    if (controllerRef.checked) {
      for (const input of inputRefs) {
        input.checked = true;
      }
    } else {
      for (const input of inputRefs) {
        input.checked = false;
      }
    }
  };

  return (
    <div className='lights-out'>
      {
        [...Array(N * N).keys()].map((n) => {
          if (n === 0) {
            return(
              <input
                className={ `lights-out__square lights-out__square--${n} lights-out__controller` }
                key={ n }
                onClick={ toggleController }
                onMouseUp={ (e) => { e.target.blur(); } }
                ref={ setRef }
                type='checkbox' />
              );
          } else if (n <= (N - 1) || n % N === 0) {
            return (
              <input
                className={ `lights-out__square lights-out__square--${n}` }
                key={ n }
                onMouseUp={ (e) => { e.target.blur(); } }
                ref={ setRef }
                type='checkbox' />
            );
          } else {
            return (
              <div
                className={ `lights-out__square lights-out__square--${n}` }
                key={ n } />
            );
          }
        })
      }
    </div>
  );
};

export default LightsOut;
